header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  background-color: #333;
  color: #fff;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

header h1{
  margin: 0 20px;
}
header nav{
  display: flex;
  align-items: center;
}
header nav a{
  color: #fff;
  text-decoration: none;
  margin-left: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
header nav a:hover{
  text-decoration: underline;
}
header nav a:active{
  color: red;
}
header nav ul{
  display: flex;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0 20px;
}
.search{
  display: flex;
  justify-content: center;
  margin:  20px 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.search input{
  padding: 7px 13px;
  font-size: 16px;
  border: 1px solid #ccc;
  margin: 0;
}
.search button{
  background-color: #bc2323;
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}
.search button:hover{
  transition: 0.2s;
  background-color: #f00;
}
.Main{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.card img{
  width: 300px;
  height: 320px;
  max-width: 300px;
  min-width: 300px;
  max-height: 320px;
  min-height: 300px;
  border: 1px solid #ccc;
  margin: 10px;
  cursor: pointer;
  transition: 0.2s;
  border-radius: 10px;
}
.card img:hover{
  transition: 0.2s;
  transform: scale(1.05);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.card{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid #ccc;
  padding: 20px;
  margin: 10px;
  background-color: #e4e4e45d;
  width: 300px;
  height: 420px;
 
}
.card .name{
  font-size: 24px;
  font-weight: bold;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  flex-direction: column;
  margin: 0;
}
.card .text{
  font-size: 16px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  flex-direction: column;
  margin: 10px;
  text-align: center;
  
}
.card button{
  font-size: 19px;
  padding: 5px 7px;
  border: 1px solid black;
  outline: none;
  margin-left: 20px;
  cursor: pointer;
  background-color: #de2713;
  color: white;
  border-radius: 5px;
  float: right;
  margin-right: 10%;
  border: 1px solid;
}
.card button:hover{
  transition: 0.2s;
  background-color: #ff0000b9;
  color: black
}
#together{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.details-card{
  display: flex;
  flex-direction: column;
  justify-self: start;
  width: 300px;
  background-color: #ffffff;
  margin-left: 50px;
}
.details-card .name{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  flex-direction: column;
  margin: 0;
}
.details-card .text{
  font-size: 16px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  flex-direction: column;
  margin: 10px;
  text-align: center;
  
}
.details-card button{
  font-size: 19px;
  padding: 5px 7px;
  border: 1px solid black;
  outline: none;
  margin-left: 20px;
  cursor: pointer;
  background-color: #de2713;
  color: white;
  border-radius: 5px;
  float: right;
  margin-right: 10%;
  border: 1px solid;}
.img-d{
  
  width: 350px;
  height: 400px;
  align-items: flex-start;
  justify-content: start;
}
#together .year{
  color: red;
}
#together .director{
  color: blue;
}
#signOutP{
  cursor: pointer;
}
#signOutP:hover{
  color: red;
}