.sign-up-container {
    width: 300px;
    margin: 50px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.715);
    font-family: Arial, sans-serif;
  }
  
  .sign-up-form {
    margin-top: 20px;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 15px;
  }
  
  .form-group:last-child {
    margin-bottom: 20px;
    position: relative;
  }
  
  .form-group:last-child::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #ccc;
  }
  
  .form-group input[type="checkbox"] {
    margin-right: 5px;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: #333;
  }
  
  .form-group input {
    width: 100%;
    height: 40px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .form-group input:focus {
    outline: none;
    border-color: #4CAF50;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .sign-up-form button[type="submit"] {
    width: 100%;
    height: 40px;
    background-color: #4CAF50;
    color: #fff;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  
  .sign-up-form button[type="submit"]:hover {
    background-color: #3e8e41;
  }
  
  .sign-up-form button[type="submit"]:active {
    background-color: #2f6d31;
  }
  
  .form-group .error-message {
    position: absolute;
    top: 100%;
    left: 0;
    font-size: 12px;
    color: #f00;
  }
  
  .form-group .success-message {
    position: absolute;
    top: 100%;
    left: 0;
    font-size: 12px;
    color: #0f0;
  }